/* -----------------------------------------------------------------------------
  # Custom Fonts
----------------------------------------------------------------------------- */
@font-face {
  font-family: 'clear';
  src: url(../fonts/clear.eot?1maawd);
  src: url(../fonts/clear.eot?1maawd#iefix) format("embedded-opentype"), url(../fonts/clear.ttf?1maawd) format("truetype"), url(../fonts/clear.woff?1maawd) format("woff"), url(../fonts/clear.svg?1maawd#clear) format("svg");
  font-weight: normal;
  font-style: normal;
}

[class^='cm-icon-'],
[class*='cm-icon-'] {
  font-family: 'clear' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.cm-icon-arrow::before {
  content: '\ea50';
}

/* -----------------------------------------------------------------------------
  # Overlay
----------------------------------------------------------------------------- */
.cm-overlay {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  background: black;
  opacity: 1;
  visibility: hidden;
  transition: all 0.25s ease-in-out;
  overflow: scroll;
  -webkit-transform: translateZ(0);
  -webkit-backface-visibility: hidden;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
}

.cm-overlay * {
  color: white;
}

.cm-reveal-top {
  transform: translateY(-100%);
}

.cm-reveal-bottom {
  transform: translateY(100%);
}

.cm-reveal-left {
  transform: translateX(-100%);
}

.cm-reveal-right {
  transform: translateX(100%);
}

.cm-reveal-fade {
  opacity: 0;
}

.cm-is-visible {
  visibility: visible;
  transform: translateY(0);
  opacity: 1;
}

/* -----------------------------------------------------------------------------
  # Menu
----------------------------------------------------------------------------- */
.cm-menu {
  margin: 0;
  padding: 0;
  position: relative;
  top: 20%;
  width: 90%;
  list-style-type: none;
  display: flex;
  flex-flow: column nowrap;
}

@media screen and (orientation: landscape) and (max-width: 768px) {
  .cm-menu {
    width: 50%;
    flex-flow: row wrap;
  }
}

.cm-menu li {
  flex: 1 auto;
}

@media screen and (orientation: landscape) and (max-width: 768px) {
  .cm-menu li {
    flex: 1 50%;
  }
}

.cm-menu a {
  position: relative;
  display: block;
  padding: 0.25em 0;
  overflow: hidden;
  text-decoration: none;
  color: white;
  font-size: 1.75rem;
  font-family: inherit;
}

.cm-menu a:hover {
  color: rgba(255, 255, 255, 0.5);
  transition: all 0.35s ease-in-out;
}

/* ---------------------------------------------------------
  ## Sub Menu
--------------------------------------------------------- */
/* -----------------------------------------------------------------------------
  # Sub Menu Trigger
----------------------------------------------------------------------------- */
.cm-submenu-trigger {
  background: transparent;
  border: none;
  flex: auto;
  order: 2;
  display: flex;
  justify-content: flex-end;
}

.cm-submenu-trigger img {
  width: 30px;
  height: auto;
}

.cm-submenu-trigger::after {
  content: '\ea50';
  font-family: 'clear' !important;
  font-size: 3rem;
  color: white;
  line-height: 1;
  speak: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 0;
  transform: rotate(90deg);
  transition: all 0.2s ease-in-out;
}

.cm-submenu-trigger .cm-has-submenu-open::after {
  transform: rotate(180deg);
  height: 25px;
  margin-right: 10px;
}

/*------------------------------------------------------------------------------
  # Hamburger Button - Modified from https://github.com/jonsuh/hamburgers
------------------------------------------------------------------------------*/
.cm-hamburger {
  position: fixed;
  top: 5%;
  right: 5%;
  z-index: 100;
  padding: 10px 10px;
  display: flex;
  cursor: pointer;
  background-color: #eeeeee;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
}

.cm-hamburger:hover {
  opacity: 0.7;
  background: none;
}

.cm-hamburger-bun {
  width: 40px;
  height: 24px;
  display: inline-block;
  position: relative;
}

.cm-hamburger-meat {
  display: block;
  top: 50%;
  margin-top: -2px;
}

.cm-hamburger-meat, .cm-hamburger-meat::before, .cm-hamburger-meat::after {
  width: 40px;
  height: 4px;
  background-color: black;
  border-radius: 4px;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}

.cm-hamburger-meat::before, .cm-hamburger-meat::after {
  content: '';
  display: block;
}

.cm-hamburger-meat::before {
  top: -10px;
}

.cm-hamburger-meat::after {
  bottom: -10px;
}

/* ---------------------------------------------------------
  ## Slider Hamburger
--------------------------------------------------------- */
.cm-hamburger-slider .cm-hamburger-meat {
  top: 2px;
}

.cm-hamburger-slider .cm-hamburger-meat::before {
  top: 10px;
  transition-property: transform, opacity;
  transition-timing-function: ease;
  transition-duration: 0.2s;
}

.cm-hamburger-slider .cm-hamburger-meat::after {
  top: 20px;
}

.cm-hamburger-slider.cm-is-active .cm-hamburger-meat {
  transform: translate3d(0, 10px, 0) rotate(45deg);
  background-color: #da4949;
}

.cm-hamburger-slider.cm-is-active .cm-hamburger-meat::before {
  transform: rotate(-45deg) translate3d(-5.71429px, -6px, 0);
  opacity: 0;
  background-color: #da4949;
}

.cm-hamburger-slider.cm-is-active .cm-hamburger-meat::after {
  transform: translate3d(0, -20px, 0) rotate(-90deg);
  background-color: #da4949;
}
